
.sidebar {
  background: #42b983;
  color: white;
  position: fixed;
  z-index: 20;
  left: 0;
  top: 0;
  bottom: 0;
  padding: 2vw 0 0;
  width: 240px;
  transition: -webkit-transform 0.2s ease;
  transition: transform 0.2s ease;
  transition: transform 0.2s ease, -webkit-transform 0.2s ease;
  -webkit-transform: translate3d(-100%, 0, 0);
          transform: translate3d(-100%, 0, 0);
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
}
.open {
  -webkit-transform: translate3d(0, 0, 0);
          transform: translate3d(0, 0, 0);
  box-shadow: 4px 0 32px -4px rgba(27, 75, 53, 0.6);
}
.disabled:not(:hover) {
  -webkit-animation-name: fadescale;
          animation-name: fadescale;
  -webkit-animation-duration: 8s;
          animation-duration: 8s;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}
@-webkit-keyframes fadescale {
50% {
    -webkit-filter: grayscale(0.5) brightness(1.1);
            filter: grayscale(0.5) brightness(1.1);
}
100% {
    -webkit-filter: grayscale(1) brightness(1.4);
            filter: grayscale(1) brightness(1.4);
}
}
@keyframes fadescale {
50% {
    -webkit-filter: grayscale(0.5) brightness(1.1);
            filter: grayscale(0.5) brightness(1.1);
}
100% {
    -webkit-filter: grayscale(1) brightness(1.4);
            filter: grayscale(1) brightness(1.4);
}
}
.note-list {
  padding: 0;
  box-shadow: 0 -4px 8px -1px #42b983 inset;
  overflow: auto;
}
.note {
  display: block;
  border-top: solid 1px #eee;
  padding: 1em;
  line-height: 1.5;
  margin: 0;
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
  text-decoration: none;
  color: white;
}
.btn {
  padding: 0.25em;
  border: none;
  background: none;
  font-size: 36px;
  color: white;
  text-shadow: -2px 1px 6px rgba(20, 20, 20, 0.4);
}
.btn:focus {
  outline: none;
  text-shadow: -4px 1px 8px rgba(20, 20, 20, 0.4);
}
.menu-btn {
  z-index: 30;
  position: fixed;
  bottom: 0;
  left: 0;
  transition: color 0.15s;
}
.invert {
  color: #333;
}
.add-btn {
  left: 180px;
  -ms-flex-negative: 0;
      flex-shrink: 0;
}



