
.textarea-wrapper {
  padding: 1em 1em 56px 1em;
  max-width: 64em;
  margin: 0 auto;
  width: 100%;
  height: 100vh;
  position: relative;
}
.textarea-wrapper:invalid textarea {
  border: solid 1px rgba(230, 230, 230, 0);
}
textarea {
  border: solid 1px rgba(22, 22, 22, 0.01);
  box-shadow: 3px 3px 120px -4px rgba(33, 33, 33, 0.25);
  width: 100%;
  min-height: 100vh;
  padding: 4vw;
  font-size: calc(1em + 0.5vw);
  line-height: 1.4;
  transition: box-shadow 0.3s cubic-bezier(0.4, 0, 1, 1);
  border-radius: 2px;
  overflow: hidden;
}
textarea:focus {
  outline: none;
  box-shadow: 1px 1px 72px -1px rgba(33, 33, 33, 0.25);
}
.textarea-wrapper:not(:invalid) textarea:focus {
  box-shadow: none;
}
.copy-btn {
  position: fixed;
  right: 0;
  bottom: 0;
}



